import { createStore } from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import router from '@/router';
import api from '@/services/api';
import { AxiosError, AxiosResponse } from 'axios';

export default createStore({
    state: {
        persistant: {
            token: '',
        },
        global_loading: false,
        selected_server: {
            id: '',
            icon_url: '',
            name: '',
        },
        profile: {
            id: '',
            nick: '',
            name: '',
            avatar_url: '',
            top_role: '',
            admin: false,
            level: 0,
            tag: '',
            joined_at: '',
            hype_squad: '',
            boosting_since: '',
            total_xp: 0,
            text_msg_xp: 0,
            voice_xp: 0,
            boost_xp: 0,
            boost_xp_multiplier: 0,
            promo_boost_xp_multiplier: 0,
            boosts: [],
            promo_boosts: [],
            boosting: false,
            boosting_name: '',
            boosting_remaining_days: 0,
            boosting_remaining_hours: 0,
            promo_code_expires_hours: 0,
            can_redeem_promo_code: false,
            promo_user_set_level: 0,
        }
    },
    mutations: {
        login(state, token: string) {
            state.persistant.token = token;
        },
        logout(state) {
            state.persistant.token = '';
            state.global_loading = false;
        },
        choose_server(state, id: string) {
            state.global_loading = true;
            api.get_guild(id).then((response: AxiosResponse) => {
                state.selected_server.id = response.data.id;
                state.selected_server.name = response.data.name;
                state.selected_server.icon_url = response.data.icon_url;

                api.get_profile().then((response: AxiosResponse) => {
                    console.log(response.data);
                    state.profile.id = response.data.id;
                    state.profile.nick = response.data.nick;
                    state.profile.name = response.data.name;
                    state.profile.avatar_url = response.data.avatar_url;
                    state.profile.top_role = response.data.top_role;
                    state.profile.admin = response.data.admin;
                    state.profile.level = response.data.lvl;
                    state.profile.tag = response.data.tag;
                    state.profile.joined_at = response.data.joined_at;
                    state.profile.hype_squad = response.data.hype_squad;
                    state.profile.boosting_since = response.data.boosting_since;
                    state.profile.total_xp = response.data.total_xp;
                    state.profile.text_msg_xp = response.data.text_msg_xp;
                    state.profile.voice_xp = response.data.voice_xp;
                    state.profile.boost_xp = response.data.boost_xp;
                    state.profile.boost_xp_multiplier = response.data.boost_xp_multiplier;
                    state.profile.promo_boost_xp_multiplier = response.data.promo_boost_xp_multiplier;
                    state.profile.boosts = response.data.boosts;
                    state.profile.promo_boosts = response.data.promo_boosts;
                    state.profile.boosting = response.data.boosting;
                    state.profile.boosting_name = response.data.boosting_name;
                    state.profile.boosting_remaining_days = response.data.boosting_remaining_days;
                    state.profile.boosting_remaining_hours = response.data.boosting_remaining_hours;
                    state.profile.promo_code_expires_hours = response.data.promo_code_expires_hours;
                    state.profile.can_redeem_promo_code = response.data.can_redeem_promo_code;
                    state.profile.promo_user_set_level = response.data.promo_user_set_level;
                    

                    state.global_loading = false;

                }).catch((e: AxiosError) => {
                    if (e.response) {
                        console.log(e.response);
                    }
                    state.global_loading = false;
                    state.persistant.token = '';
                    router.push('/login');
                });

            }).catch((e: AxiosError) => {
                if (e.response) {
                    console.log(e.response);
                }
                state.global_loading = false;
                state.persistant.token = '';
                router.push('/login');
            });
            
        
        },
    },
    plugins: [createPersistedState({
        paths: ['persistant']
      })]
})
