
import { defineComponent } from "vue";
import store from '@/store'

export default defineComponent({
  name: "Your Profile",
  methods: {
      avatar_url() {
        return store.state.profile.avatar_url;
      },
      username() {
        return store.state.profile.name;
      },
      nickname() {
        return store.state.profile.nick;
      },
      top_role() {
        return store.state.profile.top_role;
      },
      level() {
          return store.state.profile.level;
      },
      user_id() {
          return store.state.profile.id;
      },
      tag() {
          return store.state.profile.tag;
      },
      joined_at() {
          return store.state.profile.joined_at;
      },
      hype_squad() {
        return store.state.profile.hype_squad;
      },
      boosting_since() {
        return store.state.profile.boosting_since;
      },
      total_xp() {
        return store.state.profile.total_xp;
      },
      boost_xp_multiplier() {
        return store.state.profile.boost_xp_multiplier;
      },
      promo_boost_xp_multiplier() {
        return store.state.profile.promo_boost_xp_multiplier;
      },
      boosts() {
        return store.state.profile.boosts;
      },
      promo_boosts() {
        return store.state.profile.promo_boosts;
      },
      unknown_xp_percent() {
        if (store.state.profile.text_msg_xp + store.state.profile.voice_xp + store.state.profile.boost_xp == 0) {
          return 100;
        }
        return 0;
      },
      text_msg_xp_percent() {
        if (store.state.profile.text_msg_xp + store.state.profile.voice_xp + store.state.profile.boost_xp == 0) {
          return 0;
        }
        return store.state.profile.text_msg_xp * 100 / (store.state.profile.text_msg_xp + store.state.profile.voice_xp + store.state.profile.boost_xp);
      },
      voice_xp_percent() {
        if (store.state.profile.text_msg_xp + store.state.profile.voice_xp + store.state.profile.boost_xp == 0) {
          return 0;
        }
        return store.state.profile.voice_xp * 100 / (store.state.profile.text_msg_xp + store.state.profile.voice_xp + store.state.profile.boost_xp);
      },
      boost_xp_percent() {
        if (store.state.profile.text_msg_xp + store.state.profile.voice_xp + store.state.profile.boost_xp == 0) {
          return 0;
        }
        return store.state.profile.boost_xp * 100 / (store.state.profile.text_msg_xp + store.state.profile.voice_xp + store.state.profile.boost_xp);
      },
  }
});
