
import { defineComponent } from 'vue';
import store from '@/store';
import api from '@/services/api';
import { AxiosResponse } from 'axios';

export default defineComponent({
  name: 'Boosts',
  data() {
      return {
        boost_username: '',
        boost_error_msg: '',
        redeem_code: '',
        redeem_code_error_msg: '',
        promo_code: '',
        loading_promo_code: false
      }
  },
  methods: {
    get_promo_code() {
        this.loading_promo_code = true;
        api.get_promo_code().then((response: AxiosResponse) => {
            console.log(response.data);
            this.promo_code = response.data.promo_code;
            this.loading_promo_code = false;
        }).catch((e) => {
            console.log(e);
        });
    },
    boost_user() {
        api.boost_user(this.boost_username).then((response: AxiosResponse) => {
            console.log(response.data);
            store.commit("choose_server", store.state.selected_server.id);
        }).catch((e) => {
            if (e.response && e.response.status == 400) {
                console.log(e.response.data);
                this.boost_error_msg = e.response.data.description;
            }
        });
    },
    redeem_promo_code() {
        api.redeem_promo_code(this.redeem_code).then((response: AxiosResponse) => {
            console.log(response.data);
            store.commit("choose_server", store.state.selected_server.id);
        }).catch((e) => {
            if (e.response && e.response.status == 400) {
                console.log(e.response.data);
                this.redeem_code_error_msg = e.response.data.description;
            }
        });
    },
    boost_username_change() {
        this.boost_error_msg = '';
    },
    boost_xp_multiplier() {
        return store.state.profile.boost_xp_multiplier;
    },
    promo_boost_xp_multiplier() {
        return store.state.profile.promo_boost_xp_multiplier;
    },
    boosts() {
        return store.state.profile.boosts;
    },
    promo_boosts() {
        return store.state.profile.promo_boosts;
    },
    boosting() {
        return store.state.profile.boosting;
    },
    boosting_name() {
        return store.state.profile.boosting_name;
    },
    boosting_remaining_days() {
        return store.state.profile.boosting_remaining_days;
    },
    boosting_remaining_hours() {
        return store.state.profile.boosting_remaining_hours;
    },
    promo_code_expires_hours() {
        return store.state.profile.promo_code_expires_hours;
    },
    can_redeem_promo_code() {
        return store.state.profile.can_redeem_promo_code;
    },
    promo_user_set_level() {
        return store.state.profile.promo_user_set_level;
    }
  }
});
