
import { defineComponent } from 'vue';

import SparkSidebarLink from '@/components/Spark-Sidebar-Link.vue';

import store from '@/store';

export default defineComponent({
  name: 'SparkSidebar',
  props: {
    msg: String,
  },
  components: {
    SparkSidebarLink
  },
  methods: {
    server_id(): string {
        return store.state.selected_server.id; 
    },
    server_name(): string {
        return store.state.selected_server.name;
    },
    icon_url(): string {
        return store.state.selected_server.icon_url;
    },
    avatar_url(): string {
        return store.state.profile.avatar_url;
    },
    username(): string {
        return store.state.profile.name;
    },
    is_admin(): boolean {
        return store.state.profile.admin;
    },
    logout()
    {
        console.log('Logging out...');
        store.commit('logout');
    }
  }
});
